<template>
    <div id="firstcomponent">
        <h1>{{title}}</h1>
        <a> 作者：{{ author }} </a>
        <div v-html="message"></div>
        <label for="r1">修改颜色</label><input type="checkbox" v-model="use" id="r1">
        <br/>
        <div v-bind:class="{'class1': use}">
            v-bind:class 指令
        </div>

        <label>隐藏/显示</label><input type="checkbox" v-model="show" id="s1">
        <h1 v-show="show">Hello!</h1>

        <p style="font-size:25px;">计数器: {{ counter }}</p>
        <button @click="counter++" style="font-size:25px;">点我</button>


        <ul id="ul1">
            <li v-for="item in items" :key="item">
                {{ item.message }}
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "firstcomponent",
        props: ['title'],
        data() {
            return {
                author: "ryw",
                message: "<h1>v-html输出</h1>",
                use: false,
                counter: 1,
                show: false,
                items: [
                    {message: 'Foo'},
                    {message: 'Bar'}
                ]
            }
        }
    }


</script>

<style scoped>
    .class1 {
        background: #444;
        color: #eee;
    }
</style>